    <p>Se suele pasar por alto que para las personas con discapacidades motoras, navegar por la web puede ser complicado. Mientras que para quienes usan el rat칩n es sencillo hacer clic en botones o enlaces, para ellos esto puede ser casi imposible. Si no pueden mover el rat칩n con precisi칩n, tareas como llenar formularios o navegar entre p치ginas se vuelven muy dif칤ciles. </p>
